import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'

Vue.config.productionTip = false //andubo en true

// import "vue-material-design-icons/styles.css"

Vue.prototype.$http = axios
Vue.prototype.$http.defaults.baseURL = process.env.VUE_APP_API || 'https://app-comer-despierto.herokuapp.com/api'
Vue.prototype.$http.defaults.filesURL = process.env.VUE_APP_FLILES || 'https://app-comer-despierto.herokuapp.com/files'

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
